        <div>
          <el-button @click="dispath">确认并派单</el-button>
          <el-button @click="cancle">返回修改</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { userDetailInfo } from "../../../../data/userManage";
import { ScondDay } from "utils/formData";
export default {
  name: "userDisPath",
  data() {
    return {
      main: {},
      baodanlist: [],
      liveHosState: [],
      docName: ""
    };
  },
  mounted() {
    this.getDict("12"); //住院状态
    this.detail();
  },
  methods: {
    detail() {
      this.api
        .get(`/productOrder/${this.$route.query.mainId}`)
        .then(res => {
          if (res.code == 200) {
            res.data.orderListVO.beginTime = ScondDay(
              res.data.orderListVO.beginTime
            );
            res.data.orderListVO.endTime = ScondDay(
              res.data.orderListVO.endTime
            );
            if (res.data.orderListVO.status == 0) {
              res.data.orderListVO.status = "正常";
            } else if (res.data.orderListVO.status == 1) {
              res.data.orderListVO.status = "中止";
            } else if (res.data.orderListVO.status == 2) {
              res.data.orderListVO.status = "终止";
            } else if (res.data.orderListVO.status == 3) {
              res.data.orderListVO.status = "失效";
            }
            for (var i = 0; i < this.liveHosState.length; i++) {
              if (
                res.data.template.enterHospitalState ==
                this.liveHosState[i].dKey
              ) {
                res.data.template.enterHospitalState = this.liveHosState[
                  i
                ].dValue;
              }
            }
            this.getDoc(res.data.template.doctorId);
            this.main = res.data;
            this.baodanlist.push(res.data.orderListVO);
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    getDict(id) {
      this.axios
        .get("/dict?groupId=" + id)
        .then(res => {
          if (res.data.code == 200) {
            if (id == "12") {
              this.liveHosState = res.data.data;
            }
          } else {
            this.$message(this.mes.busy);
          }
        })
        .catch(err => {
          console.log(err);
          this.$message(this.mes.busy);
        });
    },
    getDoc(id) {
      this.axios
        .get("/doctor/" + id)
        .then(res => {
          if (res.data.code == 200) {
            this.docName = res.data.data.name;
          }
        })
        .catch(err => {
          console.log(err);
          this.$message(this.mes.busy);
        });
    },
    cancle() {
      this.$router.push({
        path: "userDetail",
        query: {
          mainId: this.$route.query.mainId,
          type: this.$route.query.type,
          id: this.$route.query.id,
          cardNum: this.$route.query.cardNum,
          mobile: this.$route.query.mobile,
          userName: this.$route.query.userName
        }
      });
    },
    dispath() {
      this.$router.push("userPath?mainId=" + this.$route.query.mainId);
    }
  },
  filters: {
    formatDate: function(time) {
      if (time) {
        let date = new Date(time);
        return ScondDay(date);
      } else {
        return "";
      }
    }
  }
};
</script>
 
